import React, { useEffect, useState, useRef } from "react";

// import { PDFDocument, StandardFonts, rgb } from 'pdf-lib'

import { getStore, updStore, gen16CodeId, getLabelTranslate, blobfromArray, getQrcode, fetchEncryptStream, ObjectKeys, genNumId, qryStore, parseFloatToDecimal, parseDateMMDDYY, generateComprobanteId, genAlphCode } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, SvgIcon_Cmpt, AccountLabelDetail_Cmpt, CalendarModalPicker_Cmpt } from "../../../components";


