const Svg = SvgIcon_Cmpt();

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const DropInput = SelectDropAccount_Cmpt();


const AccountLabelDetail = AccountLabelDetail_Cmpt();

const PickProvide = PickProvider_Cmpt();
const PickProduct =PickProduct_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();




const frmNm = "new_product_entry_comprobante"

const AddEntryProduct  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const navigate = useNavigate();

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("accountPicker", {});
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);
		let date = (new Date()).getTime()+800;
		if(ffm["date"]){
			let nn = (new Date(ffm["date"])).getTime();
			date = nn?nn:date;
		}
		ffm["date"] = date; 

		let comp2save = [];
		
	
		let totalC = 0;

		let extrasEntries = [];
		let comprobantesEntries = [];
		let inventoryEntries = [];

		let arr = getStore("extraAccountListOnCmp") && ObjectKeys(getStore("extraAccountListOnCmp"));
		arr && arr?.map(async (element, indE)=>{
			let nnfrm = {...ffm};
			let dataAcc = {...getStore("extraAccountListOnCmp")[element]};
			nnfrm["account"] = dataAcc["account"];
			let accountD = getStore("account_ids")?.[dataAcc["account"]];
			if(accountD.payOrCollect){
				nnfrm["payOrCollect"] = accountD.payOrCollect;
			}
			nnfrm["subAccount"] = dataAcc["subAccount"];
			nnfrm["type"] = dataAcc["type"];
			nnfrm["amount"] = dataAcc["amount"]*1;
			nnfrm["operationId"] = gen16CodeId();
			totalC +=  dataAcc["amount"]*1;
			extrasEntries.push(nnfrm)
			comprobantesEntries.push(nnfrm)
		});

		let sumInv = 0;
		

		getStore("productListOnCmp") && getStore("productListOnCmp")?.map(async (element, indE)=>{
			
			let dataAcc = {...element};
			
			let nnfrm = {...dataAcc};
			let amount = (dataAcc["qty"]*1)*(dataAcc["price"]*1);
			nnfrm["amount"] = amount;
			nnfrm["document"] = ffm["document"].trim();
			nnfrm["date"] = date;

			//nnfrm["price"] = dataAcc["pricePerSquareFeet"]*1;
			//nnfrm["qty"] = dataAcc["squareFeet"]*1;
			
			totalC +=  amount;
			sumInv +=  amount;
			inventoryEntries.push(nnfrm)
		});





		if(sumInv > 0){
			let nnfrm3 = {...ffm};
			nnfrm3["type"] =  "debit";
			nnfrm3["account"] =  getStore("global_account_params")?.inventory;
			nnfrm3["amount"] =  sumInv;
			nnfrm3["operationId"] = gen16CodeId();
			comprobantesEntries.push(nnfrm3)
		}



		if(totalC > 0 && getStore("provider_picker").account ){
			let nnfrm3 = {...ffm};
			nnfrm3["type"] =  "credit";
			nnfrm3["account"] =  getStore("provider_picker")?.["account"];
			nnfrm3["subAccount"] =  getStore("provider_picker")?.["subAccount"];
			nnfrm3["providerId"] =  getStore("provider_picker")?.["providerId"];
			ffm["providerId"] =  getStore("provider_picker")?.["providerId"];
			nnfrm3["amount"] =  totalC * 1;
			nnfrm3["operationId"] = gen16CodeId();
			let accountDeb = getStore("account_ids")?.[getStore("provider_picker")?.["account"]];
			if(accountDeb && accountDeb.payOrCollect){
				nnfrm3["payOrCollect"] = accountDeb.payOrCollect;
			}
			comprobantesEntries.push(nnfrm3)
		}
		
		
		ffm["extrasEntries"] = extrasEntries
		ffm["inventoryEntries"] = inventoryEntries
		ffm["comprobantesEntries"] = comprobantesEntries
		ffm["entriesInvoiceId"] = gen16CodeId();
		

		let bdyq2 = {...getStore(qryStore())?.add_entry_invoice};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId
		}
		bdyq2.form=ffm;


		console.log(bdyq2)

		
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			//console.log(td)
		}
		 
 
		
		data?.modalID && CloseModal({id:data?.modalID});
		
		setTimeout(() => {
			updStore("extraAccountListOnCmp", null);
			updStore("productListOnCmp", null);
			updStore("provider_picker", null);
			updStore(frmNm, null)
			set_obs(gen16CodeId());
			confirm && confirm();
			
		}, 400);

		/** 
		 navigate({
				pathname: "/accounting-ledger" ,
				search: `?cId=${comprobanteId}`
			});

			
		*/
		
	};





	

	let accountD = getStore("account_ids")?.[getStore("accountPicker")?.account];
	let subaccountD = getStore("sub_account_ids")?.[getStore("accountPicker")?.subAccount];
	


	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "860px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Entrada de Mercancias
				</p>
				<div className="flexSpace"/>
				
			</div>

			<div className="_dsplFlx ">
				<DateCalComponent init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
               


				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["document"]} 
					label={"Documento"} 
					width={120} 
					updChanges={(e)=>updFrm("document",e)}
				/>
			</div>
			
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>
			<div className="_dsplFlx "  style={{margin: "18px 0"}}>
				<div className="flexSpace"/>
				<ProviderBox />
			</div>
			

			
			<ProductsBox />

			<ExtraExpenses />

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{getStore(frmNm)?.["document"] && getStore("provider_picker")?.account && ObjectKeys(getStore("productListOnCmp"))?.length>0?
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>
				:null}
			</div>
		

			
		</div>
	);
};

export default AddEntryProduct;





const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let newB = balance.toFixed(2);
		let amount = data?.amount ? data?.type === "debit" ?  parseFloat(data.amount) :parseFloat(data.amount) *-1  : 0;
		balance = amount + parseFloat(newB);
		
	});
	return arr && arr?.length>0 && balance === 0;
}  



const ProviderBox = (obj) => {
	const [obs, set_obs] = useState(0);


	
	const  handlePick = (v) =>{
		let nn= {
			"providerId":v.providerId,
			"account":v.account,
			"subAccount":v.subAccount,
		}
		updStore("provider_picker", nn);
		set_obs(gen16CodeId())
	}

	const  clear = () =>{
		updStore("provider_picker", null);
		set_obs(gen16CodeId())
	}

	let accountD = getStore("providers_ids")?.[getStore("provider_picker")?.providerId];
	
	return (
		<div >
				{accountD?.name ? 
				<div className="_dsplFlx " style={{margin: "18px 0"}}>
					<div className=" acc_dtls">
						<div className="_dsplFlx"  >
							<div className="code" style={{color: "#646cff"}} onClick={clear}>
								{accountD?.name} 
							</div>
						</div>
						<div className="flexSpace"/>
						
					</div>
					
				</div> :
				<PickProvide
					placeholder={"buscar una provedor"} 
					width={320}
					handleClick={handlePick}
				/>
				}
			</div>

	)
}











