const ProductsBox = ({}) => {
	const [obs, set_obs] = useState(0);

	const  updObs = () =>{
		set_obs(gen16CodeId())
    }
	

    const  clear = (v) =>{
        updStore("product_picker_in", null);
		set_obs(gen16CodeId())
    }
	
	const  handlePick = (v) =>{
		let nn= {
			"productId":v.productId,
		}
		updStore("product_picker_in", nn);
		set_obs(gen16CodeId())
	}


	let productD = getStore("products_ids")?.[getStore("product_picker_in")?.productId];
	
	
	const updFrmProducts = (f, v, k) => {
		let frm2 = getStore("product_picker_in") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("product_picker_in", frm2);
		set_obs(gen16CodeId())
	};

	const addProduct2List = () => {
		let frm2 = {...getStore("product_picker_in")} || {};
		let rstFrm = {...getStore("product_picker_in")} || {};
		let productListOnCmp = getStore("productListOnCmp") || [];

		if(frm2.dimension){
			frm2["qty"]  = 1;
			frm2["squareFeet"] = calcSf2fromInch(frm2?.dimension.split(",")[0], frm2?.dimension.split(",")[1]);
			frm2["pricePerSquareFeet"] = frm2["price"]*1 / frm2["squareFeet"]*1;
		}
		frm2["inventoryId"]  = gen16CodeId();
		frm2["alphaCode"]  =genAlphCode();
		let date =    (new Date()).getTime()+800;
		if(getStore(frmNm)?.["date"]){
			let nn = (new Date(getStore(frmNm)["date"])).getTime();
			date = nn?nn:date;
		}
		frm2["date"] = date; 
		productListOnCmp.push(frm2)
		//let id = frm2["productId"];
		//productListOnCmp[id] = frm2;
		updStore("productListOnCmp", productListOnCmp);

		rstFrm["productId"] = null;
		rstFrm["qty"] = "";
		rstFrm["price"] = "";
		updStore("product_picker_in", rstFrm);
		set_obs(gen16CodeId())
	}

	const rmvProduct2List = (inDx) => {
		let productListOnCmp = getStore("productListOnCmp") || [];
		// delete productListOnCmp[id];
		productListOnCmp.splice(inDx, 1)
		updStore("productListOnCmp", productListOnCmp);
		set_obs(gen16CodeId())
	}

	const printInvLbs = () => {
		InventoryLabelsPdf(getStore("productListOnCmp"))
	}
	


	return (
		
		<div className="boxAddAcc" style={{margin: "18px 0"}}>

			{getStore("product_picker_in")?.productId ? 
			<div className="_dsplFlx " style={{margin: "18px 0"}}>
				<div className=" acc_dtls">
					<div className="_dsplFlx"   onClick={clear}>
						<div className="code" style={{color: "#646cff"}}>
							{productD?.upc_barcode} 
						</div>
						<div className="name">
							{productD?.name}
						</div>
					</div>
					<div className="flexSpace"/>
				</div>
			</div> :
			<PickProduct
				width={420}
				handleClick={handlePick}
			/>
			}
			
			{getStore("product_picker_in")?.productId? 
			<div className="_dsplFlx " >
				{productD.hasDimension? 
				<div className="" style={{width: 270,  marginLeft: 18 }}>
					<TextInput  
						init={getStore("product_picker_in")?.["dimension"]} 
						label={"Dimension (width,height) in inch"} 
						width={100}
						updChanges={(e)=>updFrmProducts("dimension",e)}
					/>
				</div>
				:
				<TextInput  
					init={getStore("product_picker_in")?.["qty"]} 
					label={"Cantidad"} 
					width={100} 
					type={"number"}
					updChanges={(e)=>updFrmProducts("qty",e)}
				/>}
				<div className="" style={{maxWidth: 130 , marginLeft: 7}}>
					<TextInput  
						init={getStore("product_picker_in")?.["price"]} 
						label={"Price"} 
						width={100} 
						type={"number"}
						updChanges={(e)=>updFrmProducts("price",e)}
					/>
				</div>
				
				<div className="flexSpace"/>
				{(getStore("product_picker_in")?.qty || getStore("product_picker_in")?.dimension) && getStore("product_picker_in")?.price ? 
				<div className="card">
					<button onClick={addProduct2List}>
						Agregar
					</button>
				</div>
				:null}
			</div>
			:null}
			
			<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
				<div className="tr_bar tr_bar_50 left_alg">
					Producto
				</div>
				<div className="tr_bar tr_bar_15">
					Dimension
				</div>
				<div className="tr_bar tr_bar_15">
					Qty
				</div>
				<div className="tr_bar tr_bar_15">
					Precio
				</div>
				<div className="tr_bar tr_bar_5">
					<div className="icon" onClick={printInvLbs}>
						<Svg name={"printer"} color={"#646cff"}/>
					</div>
				</div>
			</div>
			{getStore("productListOnCmp")  && getStore("productListOnCmp").length > 0  && 
				getStore("productListOnCmp")?.map((element, indE)=>
				<ListingProducts 
					key={element.productId +"_"+ indE} 
					idKey={element.productId}
					index={indE} 
					data={element} 
					deleteItem={rmvProduct2List}/>
			)}
		</div>

	)
}



const calcSf2fromInch = (w,h) =>{
	let square_inch = w*h* 0.0069444444444;
	return parseFloatToDecimal(square_inch,2);
};


const ListingProducts  = ({ data, deleteItem, idKey, index }) => {

	let productD = getStore("products_ids")?.[data?.productId];
	
	let dim = data?.dimension && data?.dimension.split(",")[0] +" X " + data?.dimension.split(",")[1];
	
	let dimenss =  data?.dimension && {
		lbl: dim + " (inch)",
		hover: data?.squareFeet + "S/F X $" +  parseFloatToDecimal(data?.pricePerSquareFeet,2)
	};

	const editSearchAccount = () => {
		deleteItem(index)
	}
	
	return (
		<div className="_dsplFlx hdr_bar">
			
			<div className="tr_bar tr_bar_50 left_alg" >
				{productD?.name}
			</div>
			<div className="tr_bar tr_bar_15" style={{marginTop:-4}}>
			{data?.dimension?
				<AccountLabelDetail 
					data={dimenss} 
					color={"#282828"}
					labelField={"lbl"}
					infoField={"hover"}
				/>
				:null}
			</div>
			<div className="tr_bar tr_bar_15">
				{parseFloatToDecimal(data?.qty,2)}
			</div>
			<div className="tr_bar tr_bar_15">
				{parseFloatToDecimal(data?.price,2)}
			</div>
			<div className="flexSpace"/>
			<div className="icon" onClick={editSearchAccount}>
				<Svg name={"outline_delete"} color={"#c62828"}/>
			</div>

		</div>
	)

}










